<template>
    <div class="goods">
        <!-- 头部返回 -->
        <van-nav-bar title="不凡" left-text="返回" left-arrow @click-left="onClickLeft" />
        <!-- banner图 -->
        <div class="banner">
            <img src="http://yanxuan.nosdn.127.net/8976116db321744084774643a933c5ce.png" alt="" width="100%">
        </div>
        <!-- 综合价格 分类-->
        <div class="title" @click="events">
            <van-row type="flex" justify="space-between" style="width: 100%;">
                <van-col span="6" align='center'>综合</van-col>
                <van-col span="6" align='center'>价格
                    <div class="iconBox">
                        <van-icon class="top" size="8" name="arrow-up" :color="current == 'up' ? 'red' : '' " />
                        <van-icon class="dow" size="8" name="arrow-down" :color="current == 'down' ? 'red' : '' " />
                    </div>
                </van-col>
                <van-col span="6" align='center'>分类</van-col>
            </van-row>
        </div>
        <!-- 商品列表 -->
        <div class="box">
            <van-row type="flex" justify="space-around">
                <van-col class="goodsItem" span="11" v-for="item in list" :key="item.id" @click="goinfo(item.id)">
                    <img :src="item.list_pic_url" alt="" width="100%">
                    <h5>{{item.name}}</h5>
                    <p>￥{{item.retail_price}}</p>
                </van-col>
                <van-col class="goodsItem" span="11"></van-col>
            </van-row>
        </div>
    </div>
</template>

<script>
// 引入接口
    import {
        goodsList
    } from "@/api/home/goods/index"

    export default {
        data() {
            return {
                list: [],
                currentStatus: "",
                current: "default", // default up down
            };
        },
        computed: {

        },
        created() {
            // console.log(this.$route.query.name);
            // if(this.$route.query.name == "hot"){

            // }
            this.init();
        },
        mounted() {

        },
        methods: {
            init() {
                // 获取商品列表接口
                goodsList({
                        isHot: this.$route.query.name == "hot" ? 1 : "",
                        isNew: this.$route.query.name == "new" ? 1 : "",
                        order: this.currentStatus
                    })
                    .then(res => {
                        console.log(res);
                        this.list = res.data.data
                    })
            },
            events() {
                // 判断价格状态
                if (this.current == "default") {
                    this.current = "up";
                    this.currentStatus = "asc"
                } else if (this.current == "up") {
                    this.current = "down";
                    this.currentStatus = "desc"
                } else {
                    this.current = "up";
                    this.currentStatus = "asc"
                }
                this.init();

            },
            // 返回
            onClickLeft() {
                this.$router.push("/home")
            },
            // 跳转商品详情页面
            goinfo(id) {
                this.$router.push(`/category/goods/info?id=${id}`)

            },
        },
    };
</script>

<style scoped lang="scss">
    .goods {
        .ce {
            position: fixed;
            top: 100px;
            left: 20px;
        }

        .title {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 10px 20px;
            background-color: #fff;

            .iconBox {
                display: inline-block;
                position: relative;

                .top {
                    display: block;
                    position: absolute;
                    top: -3px;

                }

                .dow {
                    display: block;
                    bottom: -3px;
                }
            }
        }

        .box {
            margin-top: 10px;
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            justify-content: space-between;

            .goodsItem {
                background-color: #fff;
                margin: 5px;

                h5 {
                    font-weight: normal;
                    font-size: 14px;
                    text-align: center;
                }

                p {
                    margin: 15px 0px;
                    text-align: center;
                    font-size: .4rem;
                    color: #b4282d;
                }
            }

        }

    }
</style>